<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import IndustryList from '@/components/IndustryList'
  import api from "@/api/detail-api"
  import utils from "../../../utils/util"

  export default {
    name: "index.vue",
    data() {
      return {
        type:'0',
        banner:'',
        current:0,
        data:{item:{image:[]},user:{logo:''}},
        name:'',
        phone:'',
        sex: '0',
        code:'',
        imgcode:'',
        content:'',
        captcha:'',
        consult:{},
        consultForm:{},
        showCode: true,
        outTime: 60,
        dialogFormVisible: false,
        formLabelWidth: '80px',
        rules: {
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
          ],

          sex: [
            { required: true, message: '请选择您的性别', trigger: 'change' }
          ],
          phone: [
            { required: true, message: '请填写您的手机号', trigger: 'blur' },
            {len: 11,message: '手机号码格式不正确',trigger:'blur'}
          ],
          img_code: [
            { required: true, message: '请填写图片验证码', trigger: 'blur' },
          ],
          code: [
            { required: true, message: '请输入手机验证码', trigger: 'blur' },
          ],
          content: [
            { required: true, message: '请输入您要咨询的内容', trigger: 'blur' },
          ],
        },
        form:{},
        productid:'',
      }

    },
    created(){
      console.log("created");
      this.id = this.$route.query.id;
      this.getData();
      const {id} = this;
      api.getConsult({id}).then(data => {
        this.consult = data;
        console.log(this.consult)
      });
      api.getImageCode().then(data => {
        console.log("getImageCode",data)
        this.captcha = data
      });
    },
    beforeRouteUpdate(to,from,next){

      const {id} = to.query;
      this.id = id;
      this.getData();
      api.getConsult({id}).then(data => {
        this.consult = data;
        console.log(this.consult)
      });
      api.getImageCode().then(data => {
        console.log("getImageCode",data)
        this.captcha = data
      });
      next();
    },
    methods:{
      getData(){
        const {id} = this;
        api.getData({id}).then(data => {
          if(data.item){
              data.item.detail = utils.htmlEscapt(data.item.detail)
          }
          this.banner =data.item.image[0];
          this.data = data;
        });
      },
      //收藏
      addLike:function(e,id){
        const {data} = this;
        if(data.item.is_like==0){
          api.addFollow({id,type:2}).then(data => {
            this.getData();
            console.log('收藏+++++++++++')
          });
        }else{
          api.removeFollow({id,type:2}).then(data => {
            this.getData();
            console.log('收藏-----------------')
          });
        }
      },
      //关注
      addFollow:function(e,id){
        console.log(id)
        const {data} = this;
        if(data.item.is_foll==0){
          api.addFollow({id,type:1}).then(data => {
            this.getData();
            console.log('关注+++++++++++')
          });
        }else{
          api.removeFollow({id,type:1}).then(data => {
            this.getData();
            console.log('关注-----------------')
          });
        }
      },

      changeImg:function(index,event){
        this.current=index;
        this.banner = this.data.item.image[index]
      },
      changeCaptcha:function(){
        api.getImageCode().then(data => {
          console.log(data)
          this.captcha = data
        });
      },
      validatePhone: function(phone){
        if (!phone) {
          this.$message.error('请输入手机号码');
          return false;
        }
        if(phone.length != 11){
          this.$message.error('手机号码格式不正确');
          return false;
        }
        return true;
      },
      /**
       * 获取验证码
       */
      getCode: function() {
        console.log("this.form;",this.form);
        const {phone = '',img_code=''} = this.form;
        const _this = this;
        if (!this.validatePhone(phone)) {
          return
        }


        api.getCode({phone,imgcode}).then(data => {
          this.showCode = false;
          this.$message({message:'验证码获取成功',type: 'success'});
          let intervalID = setInterval(function() {
            if (_this.outTime == 0) {
              _this.showCode = true;
              _this.outTime = 60;
              clearInterval(intervalID)
            }
            _this.outTime--;
          }, 1000)
        });
      },
      /**
       * 获取验证码
       */
      getCode2: function() {
        console.log("this.form;",this.consultForm);
        const {phone = '',imgcode=''} = this.consultForm;
        const _this = this;
        if (!this.validatePhone(phone)) {
          return
        }


        api.getCode({phone,img_code:imgcode}).then(data => {
          this.showCode = false;
          this.$message({message:'验证码获取成功',type: 'success'});
          let intervalID = setInterval(function() {
            if (_this.outTime == 0) {
              _this.showCode = true;
              _this.outTime = 60;
              clearInterval(intervalID)
            }
            _this.outTime--;
          }, 1000)
        });
      },

      consultCommit: function(e){
        const {data,consultForm:{name,phone,sex,code,imgcode,content}} = this;
        if(name==''){
          this.$message.error('姓名不能为空');
          return
        }else if(phone==''){
          this.$message.error('手机号码不能为空');
          return
        }else if(code==''){
          this.$message.error('手机验证码不能为空');
          return
        }else if(imgcode==''){
          this.$message.error('图片验证码不能为空');
          return
        }else if(content==''){
          this.$message.error('请输入留言');
          return
        }
        api.addConsult({type:0,item_id:data.item.id,name,phone,sex,code,img_code:imgcode,content}).then(data => {
          this.consultForm = {};
          this.$message.success("提交成功")
        });
      },
      /**
       * 立即咨询 提交表单
       */
      submitForm:function (formName){
        console.log(this.form);
        const {productid} = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            api.addConsult({type: this.type,item_id: productid,...this.form}).then(data => {
              this.dialogFormVisible = false;
              this.$message({message:'提交成功',type:'success'});
              this.resetForm(formName);
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    },
    components:{
      Search,
      HotOrder,
      IndustryList
    }
  }
</script>

