            height: 56px;
            img{
              display: block;
              width: 56px;
              height: 56px;
              -webkit-border-radius: 50%;
              -moz-border-radius: 50%;
              border-radius: 50%;
            }
          }
          dd{
            width: 760px;
            .inf{
              height: 30px;
              line-height: 30px;
              color: #999;
              span:nth-child(1){
                font-size: 16px;
                color: #333;
              }
            }
            .word{
              margin-top: 3px;
              line-height: 26px;
            }
          }
        }
        .lookBtn{
          display: block;
          width: 80px;
          text-align: center;
          color: #999;
          margin: 20px auto 0;
          cursor: pointer;
        }
      }
      .onceBox{
        width: 885px;
        height: auto;
        padding-bottom: 30px;
        background: #fff;
        .title{
          height: 62px;
          line-height: 62px;
          background: #F0F7FF;
          padding: 0 21px 0 24px;
          font-size: 14px;
          color: #999;
          margin-bottom: 30px;
          h3{
            font-size: 18px;
            color: #000;
          }
        }
        .conContent{
          padding:0 35px;
        }
        .row{
          width: 385px;
          .column{
            margin-bottom: 10px;
            label{
              line-height: 40px;
            }
            .sexLabel{
              margin-left: 10px;
              margin-right: 0px;
            }
            input,.checkBtn{
              height: 38px;
              line-height: 38px;
              border: 1px solid #E3E4E5;
              -webkit-border-radius: 2px;
              -moz-border-radius: 2px;
              border-radius: 2px;
              text-indent: 15px;
            }
            .textInp{
              width: 322px;
            }
            .nameInp{
              width: 222px;
            }
            .checkInp{
              width: 194px;
            }
            .checkBtn{
              float: right;
              width: 118px;
              text-indent:0;
              text-align: center;
              border-color: #5389F7;
              color: #5389F7;
              cursor: pointer;
            }
            .captcha{
              float: right;
              width: 120px;
              height: 40px;
              cursor: pointer;
            }
          }
          textarea{
            float: right;
            width: 306px;
            height: 58px;
            border: 1px solid #E3E4E5;
            margin-right: 4px;
            -webkit-border-radius: 2px;
            -moz-border-radius: 2px;
            border-radius: 2px;
            padding: 10px 15px;
            resize: none;
          }
        }
        .commitBtn{
          display: block;
          width:120px;
          height:40px;
          line-height:40px;
          background:linear-gradient(0deg,rgba(79,116,244,1) 0%,rgba(87,158,250,1) 100%);
          border-radius:2px;
          text-align: center;
          color: #fff;
          margin: 20px auto 0;
          cursor: pointer;
        }
      }
    }
    .detailRight{
      float: right;
      width: 295px;
      .companyInf{
        width: 295px;
        height: 450px;
        background: #fff;
        .title{
          padding: 0 23px;
          height: 65px;
          line-height: 65px;
          border-bottom: 1px solid #E3E4E5;
          font-size: 14px;
          color: #999;
          h3{
            font-size: 16px;
            color: #000;
          }
        }
        dl{
          padding: 27px 25px 0;
          dt{
            width: 100%;
            height: 100px;
            padding-top: 30px;
            background: #F4F5FA;
            text-align: center;
            img{
              display: block;
              width: 50px;
              height: 50px;
              -webkit-border-radius: 50%;
              -moz-border-radius: 50%;
              border-radius: 50%;
              margin: 0 auto 15px;
            }
            p{
              padding: 0 10px;
            }
          }
          dd{
            line-height: 24px;
            margin-top: 27px;
            p{
              margin-bottom: 11px;
            }
          }
        }
      }
    }
    .hotOrder,.IndustryList{
      margin-top: 20px;
    }
  }

  /* 咨询弹窗 */
  .code .el-input{
    width: 186px;
  }
  .codeRight{
    height: 44px;
    width: 105px;
    vertical-align: middle;
    display: inline-block;
    margin-top: -4px;
    line-height: 44px;
    cursor: pointer;
  }
  .codeBtn {
    height: 41px;
    overflow: hidden;
    font-size: 14px;
    color: #4272FF;
    text-align: center;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
  }

  .outTime {
    color: #666;
  }
  /* 咨询弹窗 */
</style>



