/**
 * 引入fetch、baseUrl
 * @param params
 * @returns {*}
 */
import httpUtils from 'config/index'

class detailService {
  getData(params) {
    return httpUtils.get("/index/item/item_detail",params);
  }
  addFollow(params){
    return httpUtils.get("/index/follow/add_follow",params);
  }
  removeFollow(params){
    return httpUtils.get("/index/follow/remove_follow",params);
  }
  getConsult(params){
    return httpUtils.get("/index/consult/get_consult_list",params);
  }
  addConsult(params){
    return httpUtils.get("/index/consult/add_consult",params);
  }
  getCode(params){
    return httpUtils.get("/index/login/get_code",params);
  }
  getImageCode(){
    return httpUtils.get("/index/login/imageCode");
  }
}
export default new detailService()



